import React, { useEffect, useState } from 'react';
import { Typography, Button, Container } from '@mui/material';
import { Form, Input, Select, message } from 'antd';
import { Link } from 'react-router-dom';
import { useParams } from "react-router-dom";
import axios from 'axios';

import Box from '@mui/material/Box';
import { styled, alpha } from '@mui/material/styles';

import Page from '../../../components/Page';
const { Option } = Select;

const PageTitle = styled('div')(({ theme }) => ({
	backgroundColor: `${alpha(theme.palette.primary.main, 0.08)}`
}));



export default function EcommerceProductList() {
	const [form] = Form.useForm();
	const { id } = useParams();
	const [host, setHost] = useState([])
	message.config({ top: 100, duration: 5, });

	const onFinish = (values) => {
		console.log(values)
		if (values.password && !values.confirm) {
			message.error(`Type your password only if you want to change it.`);
			return;
		}

		axios.post(`${process.env.REACT_APP_SERVER_URL}/updatemailTeamById`, values)
			.then((res) => {
				console.log(res.data)
				message.config({ top: 100, duration: 5, });
				if (res.data.flag === "success") {
					message.success(`You have successfully modified this team.`);
				}
				else if (res.data.flag === 'failed') {
					message.error(res.data.message);
				}
			})
	};

	const onFinishFailed = (errorInfo) => {
		console.log('Failed:', errorInfo);
	};

	useEffect(() => {
		axios.post(`${process.env.REACT_APP_SERVER_URL}/getHostings`)
			.then((res) => {
				setHost(res.data.result)
			})

		axios.post(`${process.env.REACT_APP_SERVER_URL}/getemailTeamByid`, { id: id })
			.then((res) => {
				form.setFieldsValue(res.data.result);
			})
	}, []);

	return (
		<>
			<Page title="Edit Email">
				<PageTitle>
					<Typography
						color="text.primary"
						sx={{
							fontSize: '23px',
							fontWeight: 'bold',
							padding: '10px',
							mb: '20px'
						}}>
						Edit Team
					</Typography>
				</PageTitle>
				<Box>
					<Container>
						<Form
							form={form}
							name="basic"
							labelCol={{
								span: 8,
							}}
							wrapperCol={{
								span: 16,
							}}
							initialValues={{
								remember: true,
							}}
							onFinish={onFinish}
							onFinishFailed={onFinishFailed}
							autoComplete="off"
						>

							<Form.Item
								style={{ display: 'none' }}
								name="id"
							>
								<Input size="large" />
							</Form.Item>

							<Form.Item
								label="Name"
								name="name"
								rules={[
									{
										required: true,
										message: 'Please type name',
									},
								]}
							>
								<Input size="large" />
							</Form.Item>

							<Form.Item
								label="Email"
								name="email"
								rules={[
									{
										required: true,
										message: 'Please select email',
									},
								]}
							>
								<Select placeholder="Select team" size="large">
									{host && host.map((val) =>
										<Option key={val.id} value={val.id}>{val.email}</Option>
									)}
								</Select>
							</Form.Item>

							<Form.Item
								wrapperCol={{
									offset: 8,
									span: 16,
								}}
							>
								<Button sx={{ mx: '15px' }} variant='contained' type="submit">
									Update
								</Button>
								<Link to="/dashboard/team">
									<Button variant='outlined'>Cancel</Button>
								</Link>
							</Form.Item>
						</Form>
					</Container>
				</Box>

			</Page>
		</>
	);
}

